<template>
  <div class="menu">
    <div class="navbar">
      <a>
        <img 
          src="./../assets/Icon@ac-icon.svg" 
          alt="Logo" 
          class="logo" 
          @click="redirectMainPage(currentUser.role)"
        />
      </a>
      <div class="navbar-options">
        <div class="navbar-list">
          <template v-if="currentUser.role !== 'admin'">
            <div
              :class="[
                'navbar-item',
                'page-item',
                { active: currentPage === 'main' },
              ]"
              @click="switchCurrentDisply('main')"
            >
              <router-link to="/main">
                <img
                  v-if="currentPage === 'main'"
                  src="./../assets/Vector_home-action.svg"
                  alt="main"
                />
                <img
                  v-else
                  src="./../assets/Vector_home-icon.svg"
                  alt="main-unselected"
                />
                
              </router-link>
            </div>
            <div
              :class="[
                'navbar-item',
                'page-item',
                { active: currentPage === 'user' },
              ]"
              @click="switchCurrentDisply('user')"
            >
              <router-link
                :to="{ name: 'user', params: { id: currentUser.id } }"
              >
                <img
                  v-if="currentPage === 'user'"
                  src="./../assets/Vector_user-action.svg"
                  alt="user-info"
                />
                <img
                  v-else
                  src="./../assets/Vector_user-icon.svg"
                  alt="user-info-unselected"
                />
                
              </router-link>
            </div>
            <div
              :class="[
                'navbar-item',
                'page-item',
                { active: currentPage === 'public-chatroom' },
              ]"
              @click="switchCurrentDisply('public-chatroom')"
            >
              <router-link :to="{ name: 'public-chatroom' }">
                <img 
                  v-if="currentPage === 'public-chatroom'"
                  src="./../assets/Vector_message-action.svg" alt="message" 
                />
                <img
                  v-else
                  src="./../assets/Vector_message-icon.svg"
                  alt="message-unselected"
                />

                
              </router-link>
            </div>
            <div
              :class="[
                'navbar-item',
                'page-item',
                { active: currentPage === 'private-chatroom' },
              ]"
              @click="switchCurrentDisply('private-chatroom')"
            >
              <router-link :to="{ name: 'private-chatroom' }">
                <img 
                  v-if="currentPage === 'private-chatroom'"
                  src="./../assets/Vector_message-action.svg" alt="message" 
                />
                <img
                  v-else
                  src="./../assets/Vector_message-icon.svg"
                  alt="message-unselected"
                />

                
              </router-link>
            </div>
            <div
              :class="[
                'navbar-item',
                'page-item',
                { active: currentPage === 'setting' },
              ]"
              @click="switchCurrentDisply('setting')"
            >
              <router-link :to="{ name: 'setting' }">
                <img
                  v-if="currentPage === 'setting'"
                  src="./../assets/Vector_cogsetting-action.svg"
                  alt="cogsetting"
                />
                <img
                  v-else
                  src="./../assets/Vector_cogsetting-icon.svg"
                  alt="cogsetting-unselected"
                />
                
              </router-link>
            </div>
          </template>
          <template v-else>
            <div
              :class="[
                'navbar-item',
                'page-item',
                { active: currentPage === 'admin-tweets' },
              ]"
              @click="switchCurrentDisply('admin-tweets')"
            >
              <router-link to="/admin/tweets">
                <img
                  v-if="currentPage === 'admin-tweets'"
                  src="./../assets/Vector_home-action.svg"
                  alt="tweets-list"
                />
                <img
                  v-else
                  src="./../assets/Vector_home-icon.svg"
                  alt="tweets-list-unselectd"
                />
                
              </router-link>
            </div>
            <div
              :class="[
                'navbar-item',
                'page-item',
                { active: currentPage === 'admin-users' },
              ]"
              @click="switchCurrentDisply('admin-users')"
            >
              <router-link to="/admin/users">
                <img
                  v-if="currentPage === 'admin-users'"
                  src="./../assets/Vector_user-action.svg"
                  alt="users-cards"
                />
                <img
                  v-else
                  src="./../assets/Vector_user-icon.svg"
                  alt="users-unselected"
                />
                
              </router-link>
            </div>
          </template>
        </div>
      </div>
      <button
        v-if="currentUser.role !== 'admin'"
        class="show-tweet-modal"
        @click="showCreateModal"
      >
        
      </button>
    </div>
    <div class="logout" @click="logout">
      <img src="./../assets/Vector_logout-icon.svg" alt="logout" />
      <span class="cursor-pointer"></span>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Toast } from "../utils/helpers";

export default {
  name: "Navbar",
  data() {
    return {
      currentPage: "",
    };
  },
  computed: mapState({
    currentUser: state => state.currentUserModule.currentUser
  }),
  // {
  //   ...mapState(["currentUser"]),
  // },
  methods: {
    showCreateModal() {
      this.$emit("show-create-modal");
    },
    logout() {
      Toast.fire({
        icon: "success",
        title: "",
      });
      this.$store.commit("currentUserModule/revokeAuthentication");
      this.$router.push("/login");
    },
    switchCurrentDisply(mode) {
      switch (mode) {
        case "main":
          this.currentPage = "main";
          break;
        case "user":
          this.currentPage = "user";
          break;
        case "setting":
          this.currentPage = "setting";
          break;
        case "public-chatroom":
          this.currentPage = "public-chatroom";
          break;
        case "private-chatroom":
          this.currentPage = "private-chatroom";
          break;
        case "admin-tweets":
          this.currentPage = "admin-tweets";
          break;
        case "admin-users":
          this.currentPage = "admin-users";
          break;
      }
    },
    redirectMainPage(role){
      if(role === 'admin'){
        this.$router.push({ name: 'admin-tweets' })
      }else{
        this.$router.push({ name: 'main' })
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.menu {
  width: 25%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow:hidden;
  .navbar {
    // height: 100%;
    padding: 14px 0 0 70px;
    border-right: 1px solid #e6ecf0;
  }
}

img.logo {
  width: 30px;
  height: 30px;
}

img {
  width: 24px;
  height: 24px;
  margin-right: 21px;
}

.logo {
  margin-bottom: 35px;
  cursor: pointer;
}

a {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
  font-weight: bold;
}

a:hover {
  color: #ff6600;
}

button {
  width: 210px;
  height: 38px;
}

.logout {
  position: fixed;
  bottom: 2rem;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0 0 14px 70px;
  font-weight: bold;
}

span:hover {
  color: #ff6600;
}

.page-item .active {
  color: #ff6600;
  z-index: 1;
}
</style>