<template>
  <div id="twitter">
    <Navbar
      v-if="isAuthenticated"
      @show-create-modal="toggleCreateTweetModal"
    />

    <router-view />

    <PopularList
      v-if="
        isAuthenticated && currentUser.role !== 'admin' && topUsersDisplayStatus
      "
    />

    <NewPostModal
      v-if="isEditingCreateModal"
      :checked="checked"
      :isProcessing="isProcessing"
      @afterSubmit="createNewTweet"
      @close-btn-clicked="toggleCreateTweetModal"
      @checkedMsg="checkedMsg"
    />
  </div>
</template>


<script >
import Navbar from "./components/Navbar.vue";
import PopularList from "./components/PopularList.vue";
import { mapState } from "vuex";
import NewPostModal from "./components/NewPostModal.vue";
import { Toast } from "./utils/helpers";
import tweetsAPI from "./apis/tweets.js";

export default {
  components: {
    Navbar,
    PopularList,
    NewPostModal,
  },

  data() {
    return {
      isEditingCreateModal: false,
      checked: false,
      isProcessing: false,
    };
  },
  computed: mapState({
    currentUser: (state) => state.currentUserModule.currentUser,
    isAuthenticated: (state) => state.currentUserModule.isAuthenticated,
    topUsersDisplayStatus: (state) =>
      state.statusControlModule.topUsersDisplayStatus,
  }),
  // ...mapState(["currentUser", "isAuthenticated","topUsersDisplayStatus"]),
  methods: {
    toggleCreateTweetModal() {
      this.isEditingCreateModal = !this.isEditingCreateModal;
    },
    async createNewTweet(payload) {
      this.isProcessing = true;
      try {
        if (payload.length === 0) {
          Toast.fire({
            icon: "warning",
            title: "",
          });
          return;
        }
        if (payload.length > 140) {
          Toast.fire({
            icon: "warning",
            title: "140",
          });
          return;
        }
        const response = await tweetsAPI.postTweet({ description: payload });
        if (response.status !== 200) throw new Error(response.statusText);
        Toast.fire({
          icon: "success",
          title: "",
        });
        this.toggleCreateTweetModal();
        this.$store.commit("statusControlModule/togglePostClickedStatus");
        this.isProcessing = false;
      } catch (error) {
        this.isProcessing = false;
        console.log("error", error);
        Toast.fire({
          icon: "error",
          title: "",
        });
      }
    },
    checkedMsg(data) {
      const { description } = data;
      if (description.length > 140) {
        this.checked = true;
      } else if (description.length < 141) {
        this.checked = false;
      }
      return;
    },
  },
};
</script>

<style lang="scss" scoped>
#twitter {
  position: relative;
  display: flex;
  max-width: 1440px;
  height: 1200px;
  margin: 0 auto;
}
</style>
